// Bounce

// This was originally two mixins: bounce-in and bounce-out
// Bounce-in used 1.2 as scale amount
// Bounce-out use 0.8 as scale amount
// You may enter any amount to scale by but the above are the recommended values
@mixin bounce($amount) {
    @include hacks();
    @include prefixed(transition-duration, $slowDuration);

    #{$hover-states} {
        @include prefixed(transition-timing-function, cubic-bezier(.47,2.02,.31,-0.36));
        @include prefixed(transform, scale($amount)); } }
