// Icon Fade
@mixin icon-fade {
    @include hacks();
    position: relative;
    padding-right: #{$spaceBetweenTextAndArrows};

    &::before {
        @include prefixed(transform, translateZ(0));
        @include prefixed(transition-duration, 0.5s);
        @include prefixed(transition-property, color);
        content: '';
        position: absolute;
        right: 1em;
        padding: 0 1px; }

    #{$hover-states} {
        &::before {
            color: #0F9E5E; } } }
