// Icon Pulse
@include keyframes(#{$nameSpace}-icon-pulse) {
    25% {
        @include prefixed(transform, scale(1.3)); }
    75% {
        @include prefixed(transform, scale(0.8)); } }

@mixin icon-pulse {
    @include hacks();
    position: relative;
    padding-right: #{$spaceBetweenTextAndArrows};

    &::before {
        @include prefixed(transform, translateZ(0));
        @include prefixed(transition-timing-function, ease-out);
        content: '';
        position: absolute;
        right: 1em;
        padding: 0 1px; }

    #{$hover-states} {
        &::before {
            @include prefixed(animation-name, #{$nameSpace}-icon-pulse);
            @include prefixed(animation-duration, 1s);
            @include prefixed(animation-timing-function, linear);
            @include prefixed(animation-iteration-count, infinite); } } }
