// Float

// Combination of float and sink mixins

// Arguments
// directions: up, down, default to up
// amount: amount the element should be translated by, defaults to 4px
// duration: duration of the sweep, defaults to $mediumDuration

// Example use:
// The following will produce an elment which, on hover, will be translated up 4px
//     .button
//         @include float

// The following will produce an element that, on hover, will sink down by 8px over 0.4s
//     .button
//         @include float(down, 8px, 0.4s)

@mixin float($direction: up, $amount: 4px, $duration: $mediumDuration) {
    @include hacks();
    @include prefixed(transition-duration, $duration);
    @include prefixed(transition-property, transform);
    @include prefixed(transition-timing-function, ease-out);

    #{$hover-states} {
        @if $direction == up {
            @include prefixed(transform, translateY(-#{$amount})); }
        @else if $direction == down {
            @include prefixed(transform, translateY(#{$amount})); } } }
