// Underline
// Use to animate an underline on an element on hover

// Arguments:
// from = the origin of the underline: left, right, center
// color = color of the line
// height = thickness of the line
// duration = length of time the animation takes
// pseudo = before or after

@mixin underline($from, $color, $height, $duration: $mediumDuration, $pseudo: before) {
    @include hacks();
    position: relative;
    overflow: hidden;

    &::#{$pseudo} {
        @include prefixed(transition-property, "left, right");
        @include prefixed(transition-duration, $duration);
        @include prefixed(transition-timing-function, ease-out);
        content: '';
        position: absolute;
        z-index: -1;
        bottom: 0;
        background: $color;
        height: $height;

        @if $from == left {
            right: 100%;
            left: 0; }
        @else if $from == right {
            right: 0;
            left: 100%; }
        @else if $from == center {
            right: 50%;
            left: 50%; } }

    #{$hover-states} {
        &::#{$pseudo} {
            left: 0;
            right: 0; } } }
