// Wobble Fixed

// Creates a wobble effect for elements without moving their position.

// Arguments
// origin: top, bottom, or none - defaults to none
// duration: duration of the wobble - defaults to $mediumDuration

// Example use:
// The following will create a wobble effect from the center of an element, so both top and bottom appear to be animated.
//     .button
//         @include wobble-fixed

// The following will create a wobble effect on the bottom of an element, so the top stays fixed and the bottom is animated
//     .button
//         @include wobble-fixed(bottom)

// The following will create a wobble effect on the top of an element, so the bottom stays fixed and the top is animated. The animation will last 2 seconds.
//     .button
//         @include wobble-fixed(top, 2s)

@include keyframes(#{$nameSpace}-wobble) {
    16.65% {
        @include prefixed(transform, skew(-12deg)); }
    33.3% {
        @include prefixed(transform, skew(10deg)); }
    49.95% {
        @include prefixed(transform, skew(-6deg)); }
    66.6% {
        @include prefixed(transform, skew(4deg)); }
    83.25% {
        @include prefixed(transform, skew(-2deg)); }
    100% {
        @include prefixed(transform, skew(0)); } }


@mixin wobble-fixed($origin: none, $duration: $mediumDuration) {
    @include hacks();

    @if $origin == top {
        @include prefixed(transform-origin, 0 100%); }

    @else if $origin == bottom {
        @include prefixed(transform-origin, 100% 0); }

    #{$hover-states} {
        @include prefixed(animation-name, #{$nameSpace}-wobble);
        @include prefixed(animation-duration, 1s);
        @include prefixed(animation-timing-function, ease-in-out);
        @include prefixed(animation-iteration-count, 1); } }
